* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  color: #688A93;
}

a:hover {
  color: black;
}

body {
  font-family: 'PT Sans', sans-serif;
}

.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 50px 25px;
}

.container {
  width: 100%;
  padding: 0 25px;
  position: relative;
}

.lettersblock {
  position: absolute;
  padding: 0 0 50px 0;
}

.hidden {
  display: none;
}

.flexcontainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 1px;
}

.togglebtns {
  -ms-flex-item-align: end;
      align-self: flex-end;
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  background-color: #ECF3F5;
  border: none;
  padding: 6px 10px;
  margin: 9px 5px;
  cursor: pointer;
}

.togglebtns:hover {
  background-color: #688A93;
  color: white;
}

.togglebtnsactive {
  background-color: #688A93;
  color: white;
}

.infocontainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.langmenu {
  padding: 15px;
  width: 20%;
  min-width: 190px;
  background: #ECF3F5;
  border-radius: 10px;
}

.langtitle {
  font-size: 64px;
  line-height: 83px;
}

.langlist {
  font-size: 18px;
  line-height: 36px;
}

.langllistparams {
  list-style-type: none;
}

.langlisttitle {
  color: #E7504F;
  padding-top: 30px;
}

.langinfo {
  font-size: 24px;
  line-height: 31px;
  padding: 40px 0 10px 0;
  width: 85%;
}

.altborder {
  outline: 2px solid #688A93;
  outline-offset: -1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.altborder_in {
  outline: 4px solid white;
  outline-offset: -4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  gap: 1px;
}

.smalltitle {
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  padding: 35px 15px 15px 0;
}

.smallunititle {
  padding: 8px;
}

.letter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  width: 90px;
  height: 90px;
  background: #CEE2E7;
}

.letter:hover {
  opacity: 0.7;
  position: relative;
  filter: alpha(opacity=70);
}

.letter:hover::after {
  content: attr(data-title);
  position: absolute;
  left: 60%;
  top: 60%;
  z-index: 1;
  text-align: left;
  background: rgba(255, 255, 255, 0.95);
  font-size: 11px;
  padding: 5px 10px;
  border: 1px solid #CEE2E7;
}

.letter_uni {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  width: 90px;
  height: 90px;
  background: #CEE2E7;
}

.letter_uni:hover {
  opacity: 0.7;
  position: relative;
  filter: alpha(opacity=70);
}

.letter_uni:hover::after {
  content: attr(data-title);
  position: absolute;
  left: 60%;
  top: 60%;
  width: 150px;
  z-index: 1;
  text-align: left;
  background: rgba(255, 255, 255, 0.95);
  font-size: 11px;
  padding: 5px 10px;
  border: 1px solid #CEE2E7;
}

.notrussiansign {
  background: #E6E183;
}

.dialectsign {
  background: #9DD6BE;
}

.oldersign {
  background: #EEB4A7;
}

.lexicosign {
  background: #8FBCFF;
}

.unicode {
  padding-bottom: 10px;
  color: #959595;
  font-size: 12px;
}

.bigletter {
  font-size: 32px;
  padding-top: 23px;
}

.biguniletter {
  font-size: 32px;
  padding-bottom: 23px;
}

.markplace {
  text-align: right;
  margin: -3px 6px 10px 5px;
}

.altmark {
  font-size: 10px;
  padding: 1px 6px 3px 6px;
  border-radius: 10px;
  color: #A4A4A4;
  background: rgba(255, 255, 255, 0.95);
}

.legendbg {
  padding: 17px;
  border-radius: 10px;
  background: #ECF3F5;
  margin: 40px 0 -57px 20px;
  list-style-type: none;
}

.colorsample {
  width: 15px;
  height: 15px;
}

.legenditem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 3px;
}

.legendtext {
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  margin-left: 15px;
}
