$main-grey: #CEE2E7;
$darkgrey: #688A93;
$main-bggrey: #ECF3F5;
$grey-txt: #A4A4A4;
$main-yellow: #E6E183;
$main-green: #9DD6BE;
$main-red: #EEB4A7;
$main-blue: #8FBCFF;
$main-transparent: rgba(255, 255, 255, 0.95);
$title: #E7504F;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    font-size: 16px;
    // text-transform: uppercase;
    line-height: 24px;
    color: $darkgrey;

    &:hover {
        color: black;
    }
}

body {
    font-family: 'PT Sans', sans-serif;
}

.wrap {
    display: flex;
    width: 100%;
    padding: 50px 25px;
}

.container {
    width: 100%;
    padding: 0 25px;
    position: relative;
}

.lettersblock {
    position: absolute;
    padding: 0 0 50px 0;
}

.hidden {
    display: none;
}

.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    gap: 1px;
}

.togglebtns {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: $main-bggrey;
    border: none;
    padding: 6px 10px;
    margin: 9px 5px;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnsactive {
    background-color: $darkgrey;
    color: white;
}

.infocontainer {
    display: flex;
    // align-items: flex-start;
    width: 100%;
}

.langmenu {
    padding: 15px;
    width: 20%;
    min-width: 190px;
    background: $main-bggrey;
    border-radius: 10px;
}

.langtitle {
    font-size: 64px;
    line-height: 83px;
}

.innercontainer {
    // margin-right: 90px;
    // width: 270px;
}

.langlist {
    font-size: 18px;
    line-height: 36px;
}

.langllistparams {
    list-style-type: none;
}

.langlisttitle {
    color: $title;
    // border-bottom: 1px solid $main-grey;
    padding-top: 30px;
}

.langinfo {
    font-size: 24px;
    line-height: 31px;
    padding: 40px 0 10px 0;
    width: 85%;
}

.altborder {
    outline: 2px solid $darkgrey;
    outline-offset: -1px;
    display: flex;
    flex-wrap: nowrap;
}

.altborder_in {
    outline: 4px solid white;
    outline-offset: -4px;
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.smallunititle {
    padding: 8px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    width: 90px;
    height: 90px;
    background: $main-grey;
    // margin: 0 1px 1px 0;
}

.letter:hover {
    opacity: 0.7;
    position: relative;
    filter: alpha(opacity=70);

    &::after {
        content: attr(data-title);
        position: absolute;
        left: 60%;
        top: 60%;
        z-index: 1;
        text-align: left;
        background: $main-transparent;
        font-size: 11px;
        padding: 5px 10px;
        border: 1px solid $main-grey;
    }
}

.letter_uni {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    width: 90px;
    height: 90px;
    background: $main-grey;
}

.letter_uni:hover {
    opacity: 0.7;
    position: relative;
    filter: alpha(opacity=70);

    &::after {
        content: attr(data-title);
        position: absolute;
        left: 60%;
        top: 60%;
        width: 150px;
        z-index: 1;
        text-align: left;
        background: $main-transparent;
        font-size: 11px;
        padding: 5px 10px;
        border: 1px solid $main-grey;
    }
}

.notrussiansign {
    background: $main-yellow;
}

.dialectsign {
    background: $main-green;
}

.oldersign {
    background: $main-red;
}

.lexicosign {
    background: $main-blue;
}


.unicode {
    padding-bottom: 10px;
    color: rgb(149, 149, 149);
    font-size: 12px;
}

.bigletter {
    font-size: 32px;
    padding-top: 23px;
}

.biguniletter {
    font-size: 32px;
    padding-bottom: 23px;
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: -3px 6px 10px 5px;
}

.altmark {
    font-size: 10px;
    padding: 1px 6px 3px 6px;
    border-radius: 10px;
    color: $grey-txt;
    background: $main-transparent;
}

.legendbg {
    padding: 17px;
    border-radius: 10px;
    background: $main-bggrey;
    margin: 40px 0 -57px 20px;
    list-style-type: none;
}

.colorsample {
    width: 15px;
    height: 15px;
}

.legenditem {
    display: flex;
    padding: 3px;
}

.legendtext {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    margin-left: 15px;
}

